.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap');

:root {
  --primary-white: #fff; 
  --pirmary-light-grey: #f4f4f4;
  --primary-orange: #f9ab00;
  --primary-dark: rgba(255,255,255, 0);
  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
}

body {
  font-family: "Roberto Condensed", sans-serif !important;
}
/* Navbar */

nav {
  z-index: 2;
}

.logo {
  width: 4.6rem;
}

/* Menu Links */

nav a.nav-link {
  
  letter-spacing: 0.0375rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: var(--primary-white) !important;
  border-top: 00.1875rem solid var(--primary-dark);
}

.navbar-brand {
  max-width: 17.25rem;
  padding: 0 !important;
  height: 6rem !important;
}

.navbar-nav > li > a.active, .navbar-nav > li > a:focus {
  border-top: 0.1875rem solid var(--primary-orange);
}

.navbar-nav >li > a:hover {
  border-top: 0.1875rem solid var(--primary-orange);
}

/* Header */

.header-wraper {
  position: center !important;
  background: url(code-and-coffee.jpg) no-repeat !important;
  background-size: cover;
  background-position: center;
  height: 90vh;
}

.main-info {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  z-index: 1;
}

.main-info h1 {
  color: black;
  text-transform: uppercase;
  background: orange;
  
}

/* @media-info h1 { 
  .main-info h1 {
    font-size: 1.5rem !important;
  }
/* }  */

/* Typed Text */

.typed-text {
  font-size: 2rem;
  color: var(--primary-white);

}

/* Header Main Offer Button */

.btn-main-offer {
  border: 1px solid var(--primary-red);
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: var(--primary-white);
  margin: 2rem;
}

.btn-main-offer:hover {
  text-decoration: none;
  background-color: var(--primary-hover-red);
  color: var(--primary-white);
  transition: .2s ease-in-out;
}

/* Particles JS */

.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;

}

@media(max-width: 768px) {
  .particles-canvas {
    display: none
  }
}

/* About Me */

.photo-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-img {
  width: 16rem;
  border-radius: 50%;
  padding: 0.3rem;
  border: 1px solid var(--primary-orange);

}

.about-heading {
  color: gray;
  text-transform: uppercase;
}

@media(max-width: 468px) {
  .profile-img{
    width: 10rem;
  }
}


/* Services */

.services {
  text-align: center;
  padding-bottom: 2rem;
  background: var(--pirmary-light-grey)
}

.services h1 {
  color: black;
  text-transform: uppercase;
}

.services .circle {
  position: relative;
  margin: 0.375rem auto;
  background: var(--primary-orange);
  border-radius: 50%;
  width: 3.125rem;
  height: 3.125rem;
}

.services .icon {
  font-size: 1.4rem;
  color:white;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

}

.services .box {
  height: 16rem;
  border-bottom: 5px solid var(--primary-orange);
  padding: 0.625rem;
  margin-bottom: 1.875rem;
  background: var(--primary-white);
  transition: 0.3s ease-in-out;
}

.services .box:hover {
  background: orange;
  border-bottom: 0.3125rem solid black;

}

.services .box:hover .icon {
  color: black;
}
.services .box:hover .circle {
  background: white;
}

.services .box:hover p {
 color: white;
}

/* Portfolio */

.portfolio-wrapper {
  background: lightgray;
  padding: 3rem 0;
}

.portfolio-wrapper h1 {
  color: rgb(233, 151, 0);
}

.portfolio-image {
  width: 15rem;
  border: 1px solid orange;
  padding: 0 0.5rem;
}

.portfolio-image-box {
  position: relative;
  margin: 2;
}

/* Overflow Box */

.overflow {
  position: absolute;
  opacity: 0;
  background: black;
  width: 15rem;
  height: 8rem;
  top: 0;
  border: 1px solid red;
  cursor: pointer;
}

.portfolio-image-box:hover .overflow {
  opacity: 60%;
  transition: 0.3s ease-in-out;

}

.portfolio-icon {
  position: absolute;
  opacity: 0;
  margin-left: -8rem;
  margin-top: 3.6rem;
  color: orange;
  font-size: 1.5rem;

}

.portfolio-image-box:hover .portfolio-icon {
  opacity: 70%;
  transition: 0.3 ease-in-out;
  cursor: pointer;
}


/* OVERFLOW BOX */
.portfolio-image-popupbox {
  width: 45rem;
  padding: 0 0.5rem;

}

.hyper-link {
  cursor: pointer;
  color: orange;
}

.hyper-link:hover {
  color: red;
}


/* Media */

@media(max-width:768px) {
  .portfolio-image-popupbox {
    width: 100%;
  }
}

@media(max-height:570px) {
  .popupbox-wrapper {
    height: 100%;
  }

  .portfolio-image-popupbox {
    width: 60%;
  }
}

/* Contacts COmponent */

.contacts {
  background:tan;
  padding: 3rem 0;
}

.contacts h1 {
  color: brown;
  padding: 1.3rem 0;
  text-transform: uppercase;

}

.contacts p {
  color: black ;
  font-size: 1.2rem;

}

/* Inputs*/

.contacts input, .contacts textarea {
  width: 100%;
  box-sizing: border-box;
  background: none;
  resize: none;
  border: 0;
  border-bottom: 0.125rem solid darkgray;
  border-radius: 0;
  color:whitesmoke;
  outline: 0 !important;
  box-shadow: none !important;
  margin-bottom: 1rem;
  padding-left: 0% !important;


}

.contacts .container {
  padding: 4rem 0;

}

.contacts input:focus, .contacts textarea:focus {
  background: transparent;
  color: orangered;
  border-bottom: 00.125rem solid darkgray;
}

.contacts textarea {
  height: 12.4rem !important;
}

.contact-btn {
  margin: 1rem 0 0 0;
  width: 100%;
  background: none;

}

.line {
  position: relative;
  width: 0;
  height: 0.125;
  background: orange;
  display: inline-block;

}

input:focus+.line, textarea:focus+.line {
  width: 100%;
  transition: width 0.5s ease-in-out;
}

.error-message {
  color: white;
  background: red;
  text-align: left;

}

.success-message {
  font-weight: 900;
  color: green;

}

/* Media */

@media(max-width: 768px) {
  
  .contacts{
    overflow-x: hidden;
  }
  
  .contacts .container {
    padding: 0 1rem;
  }

  .contacts p {
    padding: 0 0.4rem;
  }
}

/* FOOTER COMPONENT */

.footer {
  background: black;
  color: darkgray;
  padding: 3rem;
}

.footer a {
  text-decoration: none;
  color: darkgray;

}

.footer a:hover {
  text-decoration: none;
  color: orange;
  cursor: pointer;
}

.footer p {
  margin-bottom: 0;
  
}